/* Container to hold both the size select and the button side by side */
.product-container {
    display: flex;
    align-items: center;  /* Vertically center the items */
    gap: 10px;            /* Add space between the dropdown and the button */
  }
  
  /* Add to Cart Button */
  .add-to-cart-button {
    background-color: black;
    color: white;
    border: 2px solid white;
    padding: 12px 24px;
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
    border-radius: 8px;
    text-transform: uppercase;
    transition: all 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  }
  
  .add-to-cart-button:hover {
    background-color: #444;
    border-color: #666;
    transform: translateY(-3px);
  }
  
  /* Size Selection Dropdown */
  .size-select {
    background-color: black;
    color: white;
    border: 2px solid white;
    padding: 12px;
    font-size: 12px;
    font-weight: bold;
    border-radius: 8px;
    text-transform: uppercase;
    appearance: none; /* Remove default arrow */
    cursor: pointer;
    transition: border-color 0.3s ease;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  }
  
  .size-select:focus {
    outline: none;
    border-color: #666;
  }
  
  .size-select option {
    background-color: #222;
    color: white;
  }
  
